import React, { useEffect, useRef, useState } from 'react';
import { useTranslation } from 'react-i18next';

interface TurnstileProps {
  siteKey: string;
  onVerify: (token: string) => void;
  onExpire?: () => void;
  onError?: () => void;
  theme?: 'light' | 'dark';
  size?: 'normal' | 'compact' | 'invisible';
  className?: string;
}

declare global {
  interface Window {
    turnstile: {
      render: (
        container: string | HTMLElement,
        options: {
          sitekey: string;
          theme?: 'light' | 'dark';
          size?: 'normal' | 'compact' | 'invisible';
          callback?: (token: string) => void;
          'expired-callback'?: () => void;
          'error-callback'?: () => void;
        }
      ) => string;
      reset: (widgetId: string) => void;
      getResponse: (widgetId: string) => string;
    };
  }
}

const Turnstile: React.FC<TurnstileProps> = ({
  siteKey,
  onVerify,
  onExpire,
  onError,
  theme = 'dark',
  size = 'normal',
  className = '',
}) => {
  const { t } = useTranslation();
  const containerRef = useRef<HTMLDivElement>(null);
  const [widgetId, setWidgetId] = useState<string | null>(null);
  const [isLoaded, setIsLoaded] = useState(false);

  useEffect(() => {
    // Charger le script Turnstile s'il n'est pas déjà chargé
    if (!window.turnstile) {
      const script = document.createElement('script');
      script.src = 'https://challenges.cloudflare.com/turnstile/v0/api.js';
      script.async = true;
      script.defer = true;
      script.onload = () => setIsLoaded(true);
      document.head.appendChild(script);
    } else {
      setIsLoaded(true);
    }
  }, []);

  useEffect(() => {
    if (isLoaded && containerRef.current && siteKey) {
      // Vérifier si un widget existe déjà dans ce conteneur
      const existingWidget = containerRef.current.querySelector('[data-turnstile-widget-id]');
      
      if (existingWidget) {
        // Si un widget existe déjà, ne pas en créer un nouveau
        return;
      }

      // Rendre le nouveau widget seulement s'il n'en existe pas déjà un
      const newWidgetId = window.turnstile.render(containerRef.current, {
        sitekey: siteKey,
        theme: theme,
        size: size,
        callback: (token: string) => {
          onVerify(token);
        },
        'expired-callback': () => {
          onExpire?.();
        },
        'error-callback': () => {
          onError?.();
        },
      });

      setWidgetId(newWidgetId);
    }
  }, [isLoaded, siteKey]); // Supprimé les dépendances qui causent des re-renders

  const reset = () => {
    if (widgetId) {
      window.turnstile.reset(widgetId);
    }
  };

  const getResponse = (): string => {
    if (widgetId) {
      return window.turnstile.getResponse(widgetId);
    }
    return '';
  };

  // Exposer les méthodes via ref si nécessaire
  React.useImperativeHandle(React.forwardRef(() => null), () => ({
    reset,
    getResponse,
  }));

  return (
    <div className={`turnstile-container ${className}`}>
      <div ref={containerRef} className="turnstile-widget" />
      {!isLoaded && (
        <div className="flex items-center justify-center p-4 text-gray-400">
          <div className="animate-spin rounded-full h-6 w-6 border-b-2 border-gray-400 mr-2"></div>
          {t('loading_captcha')}
        </div>
      )}
    </div>
  );
};

export default Turnstile;
