import { useState, useEffect } from 'react';
import { useNavigate, Link, useLocation } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { motion } from 'framer-motion';

import { supabase } from '@/integrations/supabase/client';
import { useSession } from '@/contexts/AuthContext';
import { showSuccess, showError } from '@/utils/toast';
import { useTurnstile } from '@/hooks/useTurnstile';

import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { ArrowLeft, Loader2, Terminal } from 'lucide-react';
import { FooterContent } from '@/components/layout/FooterContent';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';

import SignInForm from '@/components/auth/SignInForm';
import SignUpForm from '@/components/auth/SignUpForm';
import ForgotPasswordForm from '@/components/auth/ForgotPasswordForm';
import Turnstile from '@/components/ui/turnstile';

const Login = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const location = useLocation();

  const from = location.state?.from?.pathname || '/';

  const { session } = useSession();
  const [isLoading, setIsLoading] = useState(false);
  const [view, setView] = useState<'signin' | 'signup' | 'forgot_password'>('signin');
  const [allowRegistrations, setAllowRegistrations] = useState(false);
  const [checkingSettings, setCheckingSettings] = useState(true);
  const { verifyToken } = useTurnstile();
  const [turnstileToken, setTurnstileToken] = useState('');
  const turnstileSiteKey = '0x4AAAAAABvFFVNpOKr1qGpd';

  useEffect(() => {
    const checkRegistrationStatus = async () => {
      setCheckingSettings(true);
      const { data, error } = await supabase
        .from('app_settings')
        .select('value')
        .eq('key', 'allow_registrations')
        .single();

      if (error) {
        console.error("Error fetching registration status:", error);
        setAllowRegistrations(false);
      } else {
        setAllowRegistrations(data.value === 'true');
      }
      setCheckingSettings(false);
    };

    checkRegistrationStatus();

    const channel = supabase
      .channel('app-settings-change')
      .on(
        'postgres_changes',
        {
          event: 'UPDATE',
          schema: 'public',
          table: 'app_settings',
          filter: 'key=eq.allow_registrations',
        },
        (payload) => {
          const newSetting = payload.new as { value: string };
          setAllowRegistrations(newSetting.value === 'true');
        }
      )
      .subscribe();

    return () => {
      supabase.removeChannel(channel);
    };
  }, []);

  useEffect(() => {
    if (session) {
      navigate(from, { replace: true });
    }
  }, [session, navigate, from]);

  const onLoginSubmit = async (values: any) => {
    setIsLoading(true);
    
    try {
      // Vérifier le token Turnstile avant de procéder à la connexion
      const isVerified = await verifyToken(turnstileToken);
      if (!isVerified) {
        setIsLoading(false);
        return;
      }

      const { data, error } = await supabase.auth.signInWithPassword({
        email: values.email,
        password: values.password,
      });

      if (error) {
        if (error.message === 'Invalid login credentials') showError(t('invalid_login_credentials'));
        else if (error.message === 'Email not confirmed') {
          await supabase.auth.resend({ type: 'signup', email: values.email });
          showError(t('email_not_confirmed'));
        } else showError(t('unexpected_login_error'));
        await supabase.from('audit_logs').insert({ user_id: data?.user?.id || null, action: 'user_login_failed', details: { email: values.email, error: error.message } });
      } else if (data.user) {
        showSuccess(t('login_successful'));
        await supabase.from('audit_logs').insert({ user_id: data.user.id, action: 'user_login_success', details: { email: data.user.email } });
        navigate(from, { replace: true });
      }
    } catch (error) {
      console.error('Erreur lors de la connexion:', error);
      showError(t('unexpected_login_error'));
    } finally {
      setIsLoading(false);
    }
  };

  const onSignupSubmit = async (values: any) => {
    setIsLoading(true);
    
    try {
      // Vérifier le token Turnstile avant de procéder à l'inscription
      const isVerified = await verifyToken(turnstileToken);
      if (!isVerified) {
        setIsLoading(false);
        return;
      }

      const { error } = await supabase.auth.signUp({
        email: values.email,
        password: values.password,
        options: { data: { first_name: values.first_name, last_name: values.last_name } },
      });
      
      if (error) showError(error.message);
      else showSuccess(t('account_created_check_email'));
    } catch (error) {
      console.error('Erreur lors de l\'inscription:', error);
      showError(t('unexpected_signup_error'));
    } finally {
      setIsLoading(false);
    }
  };

  const onForgotPasswordSubmit = async (values: any) => {
    setIsLoading(true);
    
    try {
      // Vérifier le token Turnstile avant de procéder à la réinitialisation
      const isVerified = await verifyToken(turnstileToken);
      if (!isVerified) {
        setIsLoading(false);
        return;
      }

      const { error } = await supabase.auth.resetPasswordForEmail(values.email, {
        redirectTo: `${window.location.origin}/update-password`,
      });
      
      if (error) showError(error.message);
      else showSuccess(t('password_reset_email_sent'));
    } catch (error) {
      console.error('Erreur lors de la réinitialisation du mot de passe:', error);
      showError(t('unexpected_reset_error'));
    } finally {
      setIsLoading(false);
    }
  };

  // Handlers Turnstile partagés
  const handleTurnstileVerify = (token: string) => {
    setTurnstileToken(token);
  };

  const handleTurnstileExpire = () => {
    setTurnstileToken('');
  };

  const handleTurnstileError = () => {
    setTurnstileToken('');
  };

  const renderContent = () => {
    if (checkingSettings) {
      return <div className="flex items-center justify-center h-48"><Loader2 className="h-8 w-8 animate-spin text-gray-400" /></div>;
    }

    switch (view) {
      case 'signup':
        return (
          <div key="signup">
            <SignUpForm onSubmit={onSignupSubmit} isLoading={isLoading} />
            <div className="text-center text-sm text-gray-400 mt-4">{t('already_have_account')}{' '}<Button variant="link" className="p-0 h-auto text-blue-400" onClick={() => setView('signin')}>{t('sign_in')}</Button></div>
          </div>
        );
      case 'forgot_password':
        return (
          <div key="forgot_password">
            <ForgotPasswordForm onSubmit={onForgotPasswordSubmit} isLoading={isLoading} />
            <div className="text-center text-sm text-gray-400 mt-4"><Button variant="link" className="p-0 h-auto text-blue-400" onClick={() => setView('signin')}>{t('back_to_login')}</Button></div>
          </div>
        );
      case 'signin':
      default:
        return (
          <div key="signin">
            <SignInForm onSubmit={onLoginSubmit} isLoading={isLoading} />
            <div className="mt-4 space-y-2 text-center text-sm">
                <div>
                    <Button variant="link" type="button" className="p-0 h-auto text-blue-400" onClick={() => setView('forgot_password')}>{t('forgot_password')}</Button>
                </div>
                {allowRegistrations && (
                    <div className="text-gray-400">
                        {t('dont_have_account')}{' '}
                        <Button variant="link" className="p-0 h-auto text-blue-400" onClick={() => setView('signup')}>{t('sign_up')}</Button>
                    </div>
                )}
            </div>
            {!allowRegistrations && <Alert className="mt-6 bg-blue-900/30 border-blue-500/30 text-blue-300"><Terminal className="h-4 w-4" /><AlertTitle>Information</AlertTitle><AlertDescription>{t('registrations_are_closed')}</AlertDescription></Alert>}
          </div>
        );
    }
  };

  const titles = {
    signin: t('admin_login'),
    signup: t('sign_up'),
    forgot_password: t('reset_password_title'),
  };

  const descriptions = {
    signin: t('access_your_dashboard'),
    signup: t('create_a_new_account'),
    forgot_password: t('reset_password_desc'),
  };

  return (
    <div className="relative min-h-screen flex flex-col bg-gray-900 text-white">
      {/* Background avec effet de profondeur */}
      <div className="absolute inset-0 z-0">
        <div className="absolute inset-0 bg-gradient-to-br from-gray-900 via-gray-800 to-black"></div>
        <div className="absolute inset-0 bg-[url('/grid.svg')] bg-center opacity-20 [mask-image:linear-gradient(180deg,white,rgba(255,255,255,0))]"></div>
        <div className="absolute top-0 left-0 w-full h-full bg-gradient-to-b from-transparent via-gray-900/50 to-gray-900"></div>
      </div>

      {/* Bouton retour avec design amélioré */}
      <div className="absolute top-6 left-6 z-20">
        <Button 
          asChild 
          variant="ghost" 
          className="group relative overflow-hidden rounded-2xl bg-gray-800/30 backdrop-blur-md border border-gray-600/40 text-white hover:bg-gray-700/40 hover:border-gray-500/60 hover:text-white transition-all duration-300 text-base shadow-2xl hover:shadow-gray-900/50"
        >
          <Link to="/" className="flex items-center px-4 py-2">
            <ArrowLeft className="mr-3 h-5 w-5 transition-transform duration-300 group-hover:-translate-x-1" />
            <span className="font-semibold">{t('return_home')}</span>
            <div className="absolute inset-0 bg-gradient-to-r from-transparent via-white/10 to-transparent -translate-x-full group-hover:translate-x-full transition-transform duration-700"></div>
          </Link>
        </Button>
      </div>

      {/* Contenu principal avec layout agrandi */}
      <main className="relative z-10 flex-grow flex flex-col items-center justify-center p-6 sm:p-8 lg:p-12">
        <motion.div 
          initial={{ opacity: 0, y: -30 }} 
          animate={{ opacity: 1, y: 0 }} 
          transition={{ duration: 1, ease: "easeOut" }} 
          className="w-full max-w-lg lg:max-w-xl xl:max-w-2xl"
        >
          <Card className="bg-gray-800/40 backdrop-blur-xl border border-gray-600/30 text-white shadow-2xl">
            <CardHeader className="text-center pb-8">
              <CardTitle className="text-4xl lg:text-5xl font-extrabold tracking-tight bg-clip-text text-transparent bg-gradient-to-b from-white via-gray-200 to-gray-400 mb-4">
                {titles[view]}
              </CardTitle>
              <CardDescription className="text-gray-300 text-lg leading-relaxed">
                {descriptions[view]}
              </CardDescription>
            </CardHeader>
            <CardContent className="px-8 pb-8">
              {renderContent()}
              
              {/* Widget Turnstile partagé */}
              <div className="mt-6 space-y-2">
                <Turnstile
                  siteKey={turnstileSiteKey}
                  onVerify={handleTurnstileVerify}
                  onExpire={handleTurnstileExpire}
                  onError={handleTurnstileError}
                  theme="dark"
                  size="normal"
                  className="flex justify-center"
                />
              </div>
            </CardContent>
          </Card>
        </motion.div>
      </main>

      {/* Footer avec plus d'espace */}
      <footer className="relative z-10 w-full bg-transparent py-8">
        <FooterContent />
      </footer>
    </div>
  );
};

export default Login;